.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    flex-shrink: 0;
    margin-right: 4px;

    background-image: url(../images/star-active.svg);
    background-repeat: no-repeat;
    background-position: center;

    &:last-child {
      background-image: url(../images/star.svg);
      margin-right: 0;
    }
  }
}
